@import "fonts.less";
@import "reset.less";


.header {
    transition: .4s all ease;
    position: relative;
    &_top {
        height: 46px;
        background: #F6F6F6;
        &--mnu {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            height: 46px;
            ul {
                display: flex;
                align-items: center;
            }
            li {
                margin: 0 15px;
            }
            a {
                font-weight: 600;
                font-style: normal;
            }
        }
    }
    &_bottom {
        padding: 5px 0 9px;
        background-color: #fff;
        position: absolute;
        width: 100%;
        &--row {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &--mnu {
            display: flex;
            ul {
                display: flex;
                align-items: center;
            }
            li {
                margin: 0 18px;
            }
            a {
                font-size: 19px;
            }
        }
    }
    &_callback {
        margin-left: 18px;
        width: 141px;
        height: 36px;
        background: #00C66A;
        border-radius: 3px;
        font-size: 19px;
        line-height: 36px;
        color: #FFFFFF;
        text-align: center;
        transition: .4s all ease;
        &:hover {
            background: rgba(0,198,106,.5);
        }
    }
}

.header.default {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    width: 100%;
    .header_top {
        display: none;
    }
    .header_bottom {
        border-bottom: 1px solid #f6f6f6;
    }
}

.intro {
    background: #EDEDED url(../img/intro.jpg) right/contain no-repeat;
    min-height: 606px;
    position: relative;
    top: 85px;
    &_block {
        margin-top: 40px;
    }
    &_h1 {
        font-style: italic;
        font-weight: 600;
        font-size: 39px;
        line-height: 48px;
        color: #444444;
        max-width: 556px;
    }
    &_more {
        margin-top: 40px;
        background: #FFFFFF;
        box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.25);
        border-radius: 18px;
        padding: 18px 0 29px;
        &--title {
            text-align: center;
            font-style: normal;
            font-weight: 500;
            font-size: 28px;
            line-height: 34px;
            color: #444444;
        }
        &--ul {
            max-width: 530px;
            margin-top: 27px;
            margin-left: 25px;
        }
        &--li {
            margin: 5px;
            display: inline-block;
            a {
                font-size: 15px;
                color: #444;
                border-bottom: 1px dashed #444;
            }
        }
        &--input {
            display: none;
            &:checked+label {
                color: #00A9FF;
                border-color: #00A9FF;
                &:hover {
                    color: #fff;
                }
            }
        }
        &--label {
            display: inline-block;
            padding: 8px 15px;
            text-align: center;
            color: #444;
            background: #FFFFFF;
            border: 1px solid #444444;
            border-radius: 3px;
            cursor: pointer;
            transition: .4s all ease;
            &:hover {
                border-color: #00A9FF;
                background-color: #00A9FF;
                color: #fff;
            }
        }
    }
    &_numb {
        margin-left: 25px;
        display: flex;
        &--item {
            margin-top: 15px;
            &:nth-child(2) {
                margin-left: 15px;
            }
        }
        &--p {
            font-size: 15px;
            line-height: 18px;
            color: #444444;
        }
        &--input {
            margin-top: 2px;
            width: 110px;
            height: 36px;
            background: #FFFFFF;
            border: 1px solid #444444;
            border-radius: 3px;
            display: flex;
            align-items: center;
            justify-content: space-around;
            .numb {
                width: 31px;
                height: 29px;
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 29px;
                color: #444444;
                text-align: center;
            }
            .minus,.plus {
                font-weight: 600;
                font-size: 24px;
                width: 31px;
                cursor: pointer;
                text-align: center;
            }
        }
    }
    &_blue {
        background: #DCF1FB;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 30px;
        padding: 0 30px 0 160px;
        &--price {
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 20px;
            color: #444444;
            span {
                font-size: 40px;
                line-height: 48px;
            }
        }
        &--btn {
            width: 247px;
            height: 61px;
            background: #00C66A;
            border-radius: 3px;
            font-style: normal;
            font-weight: 500;
            font-size: 26px;
            line-height: 61px;
            color: #FFFFFF;
            text-align: center;
            transition: .4s all ease;
            &:hover {
                background: rgba(0,198,106,.5);
            }
        }
    }
}

.trig {
    margin-top: 50px;
    &_title {
        font-style: normal;
        font-weight: 500;
        font-size: 36px;
        line-height: 44px;
        color: #444444;
        text-align: center;
    }
    &_item {
        margin-top: 25px;
        display: flex;
        align-items: flex-start;
        &--text {
            margin-left: 8px;
        }
        &--title {
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 24px;
            color: #444444;
            margin-top: 7px;
        }
        &--desc {
            font-size: 15px;
            line-height: 18px;
            color: #444444;
            margin-top: 9px;
        }
    }
}

