    }
}

.client {
    margin-top: 48px;
    &_row {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &_left {
        padding-left: 25px;
        &--title {
            font-size: 33px;
            line-height: 40px;
            color: #444444;
        }
        &--block {
            display: flex;
            align-items: center;
            margin-top: 3px;
        }
        &--stars {
            display: flex;
            align-items: center;
            img {
                margin: 0 2px;
            }
        }
        &--number {
            font-size: 39px;
            line-height: 48px;
            color: #444444;
            margin-left: 20px;
        }
    }
    &_right {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #444444;
        max-width: 720px;
    }
}

.check {
    background: #F6F6F6;
    margin-top: 60px;
    padding: 55px 0 52px;
    min-height: 417px;
    &_title {
        text-align: center;
        font-weight: 500;
        font-size: 36px;
        line-height: 44px;
        color: #444444;
    }
    &_desc {
        text-align: center;
        font-size: 27px;
        line-height: 33px;
        margin-top: 11px;
        color: #444;
    }
    &_item {
        margin-top: 42px;
        background: #FFFFFF;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
        border-radius: 10px;
        padding: 15px 12px 40px;
        position: absolute;
        &--down {
            position: absolute;
            bottom: 13px; right: 20px;
            cursor: pointer;
            &.active {
                transform: rotate(180deg);
            }
        }
        &--h3 {
            text-align: center;
            font-weight: 500;
            font-size: 23px;
            line-height: 28px;
            color: #444444;
        }
        &--p {
            font-size: 13px;
            line-height: 16px;
            color: #444444;
            margin-top: 14px;
        }
        &--more {
            display: inline-block;
        }
    }
}

.reviews {
    margin-top: 50px;
    &_title {
        font-weight: 500;
        font-size: 36px;
        line-height: 44px;
        color: #444444;
        text-align: center;
    }
    &_slider {
        margin-top: 10px;
    }
    &_dots {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        bottom: -54px;
        left: 50%;
        transform: translate(-50%);
        li.slick-active button {
            background-color: #00A9FF;
        }
        button {
            cursor: pointer;
            width: 15px;
            height: 14px;
            background: #C4C4C4;
            margin: 0 7.5px;
            font-size: 0;
        }
    }
    &_arrow {
        width: 100%;
        display: flex;
        justify-content: space-between;
        position: relative;
        top: 162px;
    }
    &_arrow1 {
        cursor: pointer;
        position: relative;
        left: -50px;
    }
    &_arrow2 {
        cursor: pointer;
        position: relative;
        right: -50px;
    }
    &_item {
        background: #E4F5FC;
        border-radius: 10px;
        padding: 30px 12px;
        margin: 0 10px;
        &--top {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &--name {
            font-weight: 500;
            font-size: 23px;
            line-height: 28px;
            color: #444444;
        }
        &--date {
            font-size: 12px;
            line-height: 15px;
            color: #777777;
        }
        &--stars {
            display: flex;
            align-items: center;
            img {
                width: 19px;
                height: 19px;
                margin: 0 2px;
            }
        }
        &--desc {
            margin-top: 15px;
            font-size: 16px;
            line-height: 20px;
            color: #444444;
        }
        &--nomer {
            margin-top: 25px;
            font-size: 12px;
            line-height: 15px;
            color: #777777;
        }
    }
}

.blue {
    padding: 65px 0 52px;
    background: #DCF1FB;
    &_title {
        font-weight: bold;
        font-size: 36px;
        line-height: 44px;
        color: #444444;
        text-align: center;
    }
    &_btn {
        display: block;
        margin: 28px auto 0;
        width: 299px;
        height: 61px;
        background: #00C66A;
        border-radius: 3px;
        font-style: normal;
        font-weight: 500;
        font-size: 26px;
        line-height: 61px;
        color: #FFFFFF;
        text-align: center;
        transition: .4s all ease;
        &:hover {
            background: rgba(0,198,106,.5);
        }
    }
}

.already {
    margin-top: 45px;
    &_title {
        font-weight: 500;
        font-size: 36px;
        line-height: 44px;
        text-align: center;
        color: #444444;
    }
    &_table {
        margin-top: 22px;
    }
    &_one {
        background: #F6F6F6;
        padding: 16px 0 16px 10px;
        .already_td {
            font-weight: 600;
            font-size: 20px;
            line-height: 24px;
            color: #444444;
