        } 
    }
    &_td {
        padding: 27px 0 13px 10px;
        font-size: 16px;
        line-height: 20px;
        color: #444444;
    }
}

.faq {
    margin-top: 55px;
    &_title {
        font-weight: 500;
        font-size: 36px;
        line-height: 44px;
        text-align: center;
        color: #444444;
    }
    &_block {
        margin-top: 27px;
    }
    &_item {
        margin-bottom: 15px;
    }
    &_top {
        background: #FFFFFF;
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        padding: 16px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        transition: .4s all ease;
        &:hover {
            background: #00A9FF;
        }
        &:hover .faq_top--p {
            color: #fff;
        }
        &:hover .faq_top--btn path {
            fill: #fff;
        }
        &.active .faq_top--btn {
            transform: rotate(180deg);
        }
        &--p {
            font-weight: 600;
            font-size: 20px;
            line-height: 24px;
            color: #444444;
        }
        &--btn {
            cursor: pointer;
            position: relative;
            transition: .4s all ease;
        }
    }
    &_body {
        margin-top: 15px;
        background: #FFFFFF;
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        padding: 16px;
        padding-bottom: 70px;
        &--title {
            font-weight: 600;
            font-size: 20px;
            line-height: 24px;
            color: #444444;
        }
        &--p {
            margin-top: 17px;
            font-size: 16px;
            line-height: 20px;
            color: #444444;
        }
    }
}

.experience {
    margin-top: 80px;
    background: #F6F6F6;
    padding: 70px 0 100px;
    &_a {
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); 
        img {
            margin: auto;
