}

.spons {
    margin-top: 55px;
    &_title {
        font-weight: 500;
        font-size: 36px;
        line-height: 44px;
        text-align: center;
        color: #444444;
    }
    &_block {
        margin-top: 55px;
    }
    &_dots {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        bottom: -35px;
        left: 50%;
        transform: translate(-50%);
