        }
    }
    &_a {
        margin-bottom: 28px;
        background: #FFFFFF;
        border: 1px solid #EEEEEE;
        border-radius: 3px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 142px;
        transition: .4s all ease;
        &:hover {
            box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
        }
    }
}

.footer {
    margin-top: 80px;
    &_top {
        padding-top: 63px;
        border-top: 5px solid #f6f6f6;
    }
    &_bottom {
        margin-top: 37px;
        padding-top: 42px;
        border-top: 5px solid #f6f6f6;
    }
    &_p {
        margin-top: 30px;
        font-size: 15px;
        line-height: 18px;
        color: #444444;
    }
    &_soc {
        margin: 10px -7px;
        display: flex;
        align-items: center;
        &--item {
            margin: 0 7px;
        }
    }
    &_h3 {
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        color: #444444;
        &--two {
            margin-top: 25px;
        }
    }
    &_ul {
        margin-top: 11px;
    }
    &_li {
        a {
            display: block;
            margin-bottom: 8px;
            font-size: 15px;
            line-height: 18px;
            color: #444444;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &_tel {
        display: flex;
        align-items: flex-start;
        margin-top: 11px;
        &--text {
            margin-left: 10px;
            a {
                display: block;
                font-weight: 600;
                font-size: 16px;
                line-height: 20px;
                color: #444444;
            }
        }
    }
    &_pl {
        margin-top: 11px;
    }
    &_bt {
        margin-top: 27px;
    }
    &_line {
        margin-top: 10px;
        height: 55px;
        background: #F6F6F6;
        display: flex;
        align-items: center;
        justify-content: center;
        &--p {
            font-weight: 600;
            font-size: 18px;
            line-height: 22px;
            color: #444444;
        }
    }
    &_logo {
        margin-top: 16px;
        display: block;
        img {
            width: 107.26px;
            height: 50px;
        }
    }
}

.btn_mnu {
    width: 50px;
    height: 50px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: relative;
    display: none;
}
.btn_row {
    height: 2px;
    width: 60%;
    margin: 2px 0;
    background-color: #000;
    position: relative;
    transition: all .5s;
}
.btn_mnu.active .btn_row:first-child {
    transform: rotate(-135deg);
    top: 6px;
}
.btn_mnu.active .btn_row:last-child {
    transform: rotate(-225deg);
    top: -6px;
}
.btn_mnu.active .btn_row:nth-child(2) {
    opacity: 0;
}

.hidden {
    overflow: hidden;
    position: relative;
}

.menu {
    position: fixed;
    right: 0px; top: 84px;
    height: 100%;
    width: 300px;
    transition: .4s all ease;
    background-color: #f6f6f6;
    opacity: 0;
    z-index: -1;
    &.active {
        z-index: 10;
        opacity: 1;
    }
    &_top {
        background-color: #fff;
        padding: 25px 0;
        li {
            padding: 10px;
            border-bottom: 1px solid #f6f6f6;
        }
        &--item {
            font-size: 18px;
            color: #444;
        }
        &--callback {
            display: block;
            margin: 10px 0 0 10px;
            width: 141px;
            height: 36px;
            background: #00C66A;
            border-radius: 3px;
            font-size: 16px;
            line-height: 36px;
            color: #FFFFFF;
            text-align: center;
            transition: .4s all ease;
        }
    }
    &_bottom {
        background-color: #f6f6f6;
        padding: 25px 10px;
        li {
            padding: 10px 0;
        }
        &--item {
            color: #444;
            font-size: 16px;
            font-weight: 500;
        }
    }
}

@media screen and (max-width:1200px) {
    .work_img {
        display: none;
    }
    .work_block {
        background: #fff;
        border: 1px solid #00A9FF;
        padding: 30px;
        width: 100%;
        height: auto;
    }
    .reviews_slider {
        margin-top: 40px;
    }
    .work_row {
        justify-content: center;
    }
    .client_row {
        flex-direction: column;
    }
    .client_right {
        margin-top: 30px;
    }
    .check_item {
        position: relative;
    }
    .reviews_arrow,
    .experience_arrow {
        display: none;
    }
    .footer_pay {
        display: none;
    }
}

@media screen and (max-width:991px) {
    .spons_block > .row > .col-md-2:nth-child(n+7) {
        display: none;
    }
    .footer_contact {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .footer_bt ul {
        margin-bottom: 25px;
    }
}

@media screen and (max-width:768px) {
    .header_bottom--mnu ul {
        display: none;
    }
    .header_top--mnu li {
        margin: 0 10px;
    }
    .intro_blue {
        padding: 0 30px;
    }
    .intro {
        background: url(../img/intro.jpg) center/cover no-repeat;
    }
}

@media screen and (max-width:767px) {
    .footer_h3 {
        margin-top: 25px;
    }
    .already_table .col-xs-1 {
        display: none;
    }
}

@media screen and (max-width:620px) {
    .header_top {
        display: none;
    }
}

@media screen and (max-width:544px) {
    .btn_mnu {
        display: flex;
    }
    .already_td {
        font-size: 14px;
        line-height: 18px;
    }
    .already_one .already_td {
        font-size: 16px;
        line-height: 20px;
    }
    .header_callback {
        display: none;
    }
    .footer_contact {
        display: block;
    }
    .already_td {
        padding: 10px 0;
    }
