  margin-top: 10px;
  height: 55px;
  background: #F6F6F6;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer_line--p {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #444444;
}

.footer_logo {
  margin-top: 16px;
  display: block;
}

.footer_logo img {
  width: 107.26px;
  height: 50px;
}

.btn_mnu {
  width: 50px;
  height: 50px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
  display: none;
}

.btn_row {
  height: 2px;
  width: 60%;
  margin: 2px 0;
  background-color: #000;
  position: relative;
  transition: all .5s;
}

.btn_mnu.active .btn_row:first-child {
  transform: rotate(-135deg);
  top: 6px;
}

.btn_mnu.active .btn_row:last-child {
  transform: rotate(-225deg);
  top: -6px;
}

.btn_mnu.active .btn_row:nth-child(2) {
  opacity: 0;
}

.hidden {
  overflow: hidden;
  position: relative;
}

.menu {
  position: fixed;
  right: 0px;
  top: 84px;
  height: 100%;
  width: 300px;
  transition: .4s all ease;
  background-color: #f6f6f6;
  opacity: 0;
  z-index: -1;
}

.menu.active {
  z-index: 10;
  opacity: 1;
}

.menu_top {
  background-color: #fff;
  padding: 25px 0;
}

.menu_top li {
  padding: 10px;
  border-bottom: 1px solid #f6f6f6;
}

.menu_top--item {
  font-size: 18px;
  color: #444;
}

.menu_top--callback {
  display: block;
  margin: 10px 0 0 10px;
  width: 141px;
  height: 36px;
  background: #00C66A;
  border-radius: 3px;
  font-size: 16px;
  line-height: 36px;
  color: #FFFFFF;
  text-align: center;
  transition: .4s all ease;
}

.menu_bottom {
  background-color: #f6f6f6;
  padding: 25px 10px;
}

.menu_bottom li {
  padding: 10px 0;
}

.menu_bottom--item {
  color: #444;
  font-size: 16px;
  font-weight: 500;
}

@media screen and (max-width: 1200px) {
  .work_img {
    display: none;
  }

  .work_block {
    background: #fff;
    border: 1px solid #00A9FF;
    padding: 30px;
    width: 100%;
    height: auto;
  }

  .reviews_slider {
    margin-top: 40px;
  }

  .work_row {
    justify-content: center;
  }

  .client_row {
    flex-direction: column;
  }

  .client_right {
    margin-top: 30px;
  }

  .check_item {
    position: relative;
  }

  .reviews_arrow,
  .experience_arrow {
    display: none;
  }

  .footer_pay {
    display: none;
  }
}

@media screen and (max-width: 991px) {
  .spons_block > .row > .col-md-2:nth-child(n+7) {
    display: none;
  }

  .footer_contact {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .footer_bt ul {
    margin-bottom: 25px;
  }
}

@media screen and (max-width: 768px) {
  .header_bottom--mnu ul {
    display: none;
  }

  .header_top--mnu li {
    margin: 0 10px;
  }

  .intro_blue {
    padding: 0 30px;
  }

  .intro {
    background: url(../img/intro.jpg) center / cover no-repeat;
  }
}

@media screen and (max-width: 767px) {
  .footer_h3 {
    margin-top: 25px;
  }

  .already_table .col-xs-1 {
    display: none;
  }
}

@media screen and (max-width: 620px) {
  .header_top {
    display: none;
  }
}

@media screen and (max-width: 544px) {
  .btn_mnu {
    display: flex;
  }

  .already_td {
    font-size: 14px;
    line-height: 18px;
  }

  .already_one .already_td {
    font-size: 16px;
    line-height: 20px;
  }

  .header_callback {
    display: none;
  }

  .footer_contact {
    display: block;
  }

  .already_td {
    padding: 10px 0;
  }

  .intro_blue {
    flex-direction: column;
    padding: 13px 0;
  }

  .intro_blue--btn {
    margin-top: 15px;
  }

  .footer_pay {
    display: block;
    margin-top: 25px;
  }
}